main {
    margin : 10px;
}

.Menu-Card {
    width  : 300px;
    height : 150px;
    margin-top : 20px;
}

.DateTime {
    display:inline-flex;
}

.Day-1 {
    background-color: rgb(216, 77, 100);
}

.DateTime-Hour {
    width:70px;
}

.DateTime-Minute {
    width:70px;
}

.Paging-Limit {
    display: inline-flex;
    width:100px;
    height:38px;
}

.Paging-Timezone {
    display: inline-flex;
    width:200px;
    height:38px;
}

.FloatComponent {
    position:absolute;
    border: 1px solid #ced4da;
    background-color: #ffffff;
    border-radius: 4px;
}

.Progress {
    z-index  : 1024;
    position : fixed;
    right    : 20px;
    bottom   : 20px;
}

.Layout-SystemDetail > button {
    background-color:#f8d7da;
    height:20px;
}

.linkText {
  color : blue;

  background: none;
  border: none;
  cursor: pointer;
  display: inline;
  height: auto;
  width: auto;
  padding: 0;
  margin: 0;
}

.Dialogs-Card {
    max-width:350px;
    padding:20px;
    margin-bottom: 10px;
}

.Dialogs-State1 {
    background-color:lightgreen !important;
}

.Dialogs-State2 {
    background-color:lightpink !important;
}

.Dialogs-Button {
    width:100px;
}

.Layout-Breadcrumbs {
    padding:10px;
    padding-left:20px;
}

.InputGroup-Frame {
  text-align: left;
  width:340px;
  min-height: 40px;
  max-height: 40px;
  overflow: auto;
  padding:4px;
  border: 1px solid #ced4da;
  background-color: #ffffff;
  border-radius: 4px;
}

.InputGroup-Data {
  margin-right:5px;
  margin-bottom:3px;
  position: relative;
  white-space:nowrap;
  background-color:#cccccc;
  padding:5px;
  min-width:150px !important;
  max-width:150px !important;
  border-radius:3px;
}

.InputGroup-Inner {
    width:100%;
}

.InputGroup-Value {
  font-size:12px;
  text-align:left;
  float:left;
}

.InputGroup-Close {
  font-size:12px;
  position: absolute;
  right:5px;
  float:right;
}

.breadcrumb-item+.breadcrumb-item::before{
    content:var(--bs-breadcrumb-divider,">") !important ;
}

.faceImage {
  width: 100px;
  height:100px;
  border-radius:50%;
}

@media screen and (max-width: 768px) {
  .Layout-Breadcrumbs {
      display: none;
  }
}


.breadcrumb-item+.breadcrumb-item::before{
    content:var(--bs-breadcrumb-divider,">") !important ;
}

.faceImage {
  width: 100px;
  height:100px;
  border-radius:50%;
}

@media screen and (max-width: 768px) {
  .Layout-Breadcrumbs {
      display: none;
  }
}

.UserSearch {
    max-width:600px !important;
}
.small-line {
    padding:3px !important;
    margin:0px;
}